/* MEDIA QUERYS */
@media only screen and (min-width: 1921px) {
    :root {
        font-size: larger !important;
    }

    .MuiTabs-flexContainer>a {
        font-size: 1.4em !important;
    }
}

@media only screen and (max-width: 1220px) {
    #headerTexts>h1 {
        font-size: 4em !important;
    }

    .verticalProjectFigureLarger {
        min-width: 80% !important;
    }

    .MuiTabs-flexContainer>a {
        font-size: 1.3em !important;
    }
}

@media only screen and (max-width: 1020px) {
    .MuiTabs-flexContainer {
        width: 80vw !important;
    }
}

@media only screen and (max-width: 860px) {
    #headerTexts>h1 {
        font-size: 2em !important;
    }

    section.projectImages {
        grid-template-columns: 1fr !important;
        margin: 0em auto !important;
    }

    .mobileScreenShot {
        max-width: 80%;
    }

    ul#courses {
        columns: 1 !important;
        -webkit-columns: 1 !important;
        -moz-columns: 1 !important;
        padding-left: 0;
    }

    .twoArticles {
        flex-direction: column !important;
    }

    .halfWidthArticle {
        width: 100% !important;
    }

    ul {
        padding: 0 2% !important;
        align-self: flex-start !important;
        margin-left: 5%;
    }

    .longText {
        font-size: 1.8em !important;
    }
}

@media only screen and (max-width: 650px) {
    .MuiTabs-flexContainer {
        flex-direction: column !important;
    }

    .MuiTab-root {
        width: 100% !important;
    }

    #headerTexts>h2 {
        font-size: 1em;
    }
}

@media only screen and (max-width: 510px) {
    :root {
        font-size: 12px;
    }

    p {
        min-width: 92%;
    }

    ul#courses {
        padding-left: 0.2em;
    }

    .verticalProjectFigureLarger {
        height: 50% !important;
    }
}

@media only screen and (max-width: 345px) {
    #headerTexts>h1 {
        font-size: 1.3em !important;
    }

    article>h1 {
        font-size: 1.8em;
    }
}

/* COLORS */
:root {
    width: 100% !important;
    --darkGrey: #2A3439;
    --darkMint: #008080;
    --creamWhite: #FFFFF0;
    --lightMintBorder: #ACC9C2;
    --brightYellow: #ffdf22;
    --lessBrightYellow: #f6dd3c;
}


/* MAIN PARTS */

#root {
    color: var(--creamWhite);
    display: flex;
    flex-direction: column;
    font-family: "Noto Serif Khojki", serif;
    padding: 1% 2%;
}

body {
    margin: 0;
    box-sizing: border-box;
    background-color: var(--darkGrey);
    background-image: url("https://www.transparenttextures.com/patterns/classy-fabric.png");

}

main {
    max-width: 100%;
    padding: 0 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: center;
    flex-wrap: wrap;
}

header {
    max-height: 35vh;
    background: linear-gradient(145deg, rgba(2, 0, 36, 1) 0%, var(--darkMint) 35%, var(--brightYellow) 100%);
    border-radius: 10px;
    color: var(--creamWhite);
    padding: 3% 0 0 0;
    display: flex;
    justify-content: center;
}

article {
    display: flex;
    align-items: center;
    margin: 2% 0;
    padding: 3% 0;
    flex-direction: column;
    font-size: 1.3em;
}

.box_shadow {
    box-shadow: inset 0 -0.2em 0.1em var(--darkMint),
        0.2em 0.2em 0.9em var(--brightYellow);
    border-radius: 10px;
}

.twoArticles {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.halfWidthArticle {
    width: 47vw;
}

.halfWidthArticle>h1 {
    margin: 0;
}

.MuiTabPanel-root {
    width: 90%;
}

.MuiTabPanel-root>p {
    width: 100%;
}


/**** PROJECT NAVIGATION ****/

.MuiTabs-indicator {
    display: none;
}

.MuiTab-root {
    font-size: 1.7em !important;
    width: 50%;
    margin: auto;
    min-height: 4em !important;
    padding: 0 !important;
}

.MuiTab-root.Mui-selected {
    border-bottom: #ffdf22 dotted 2px !important;
    border-radius: 50px;
}

.navLinks,
.navLinks2,
.navLinks3 {
    /* margin-top: 1%; */
    margin: 1% 2% 0 2%;
}

.MuiTabs-flexContainer {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}

hr {
    height: 3px;
    width: 60%;
    margin: 4em;
    border: none;
    outline: none;
    background: linear-gradient(0.25turn, var(--darkMint), var(--creamWhite), var(--brightYellow));
}

/* TEXTS */
h1 {
    font-size: 2em;
    margin: 0 1em;
}

#headerTexts {
    grid-column: 2;
    padding: 2%;
}

#headerTexts>h1 {
    font-size: 6em;
    margin: auto;
}

#headerTexts>a {
    color: var(--darkGrey) !important;
    font-weight: bolder;
    font-size: 1.3em;
}

.multicolorText {
    background-image: linear-gradient(to right, rgb(61, 7, 61), rgb(152, 25, 25), rgb(18, 18, 118), rgb(15, 112, 15), rgb(250, 165, 54), rgb(250, 232, 70));
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.multicolorTextForHeading {
    width: min-content;
    background-image: linear-gradient(to left, rgb(61, 7, 61), rgb(152, 25, 25), rgb(18, 18, 118), rgb(15, 112, 15), rgb(250, 165, 54), rgb(250, 232, 70));
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

p {
    width: 70%;
    font-size: 1.2em;
}

#links>div {
    display: flex;
    width: 80%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.fribagoTeam,
.githubLink,
.anchorLink {
    color: var(--brightYellow);
    font-size: 1.1em !important;
}

/* WORM GAME FRAME */

iframe {
    width: 90%;
    height: 56vh;
    border: none;
}

/* SELF PORTRAIT */
#mainPortrait {
    grid-column: 1;
    max-width: 100%;
    align-self: end;
    max-height: 100%;
    opacity: 90%;
}

/* PROJECT IMAGES */
section.projectImages {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    margin: 1em 0;
    justify-content: space-between;
    align-items: end;
}

figure {
    width: 98%;
    margin: 0 auto 1em;
}

.projectImg {
    width: 100%;
    max-height: 90vh;
}

.mobileScreenShot {
    max-height: 90vh;
}

figcaption {
    min-height: 4em;
    display: flex;
    align-items: flex-end;
    font-size: 1.2em;
    margin-bottom: 0.5em;
}

#wormGameImg {
    max-width: 70%;
    max-height: 30%;
    margin-bottom: 5%;
}

.horizontalProjectFigureLarger {
    position: fixed;
    max-height: 90%;
    width: 94% !important;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 40;
    border: 2px solid var(--creamWhite);
}

.verticalProjectFigureLarger {
    position: fixed;
    height: 90%;
    max-width: 40% !important;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 40;
    border: 2px solid var(--creamWhite);
}

/* More About Me Page */
#educAndWork {
    display: flex;
    flex-direction: row;
}

ul {
    line-height: 2.5em;
}

ul#courses {
    columns: 2;
    -webkit-columns: 2;
    -moz-columns: 2;
}

ul li {
    list-style-type: none;
    font-size: 1em;
    width: 100%;
}

u {
    background-color: #FFFFF0;
    background: linear-gradient(to right, var(--brightYellow), var(--brightYellow));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 900;
}